import React from 'react'
import { Link, graphql } from 'gatsby'
import { kebabCase } from 'lodash';
import Img from 'gatsby-image'

import DefaultLayout, { ConvertDate } from '../components/layout'
import SEO from '../components/seo'
import Bio from '../components/bio'

import Social from '../components/social'

class BlogPostTemplate extends React.Component {
  render() {
    const post = this.props.data.markdownRemark
    const { previous, next } = this.props.pageContext
    const sharetitle = post.frontmatter.title + (post.frontmatter.tags.map((x) => {return " #" + x.split(' ').join('') })).toString().split(',').join('')
    const siteMetadata = this.props.data.site.siteMetadata

    return (
      <DefaultLayout>
        <SEO title={post.frontmatter.title} description={post.frontmatter.description} keywords={[post.frontmatter.tags]} cover={[post.frontmatter.img.childImageSharp.fluid.src]} />
        <article className="article-page">
          <div className="page-content">
            {post.frontmatter.img && (
              <div className="page-cover-image">
                <figure>
                  <Img
                    className="page-image"
                    key={post.frontmatter.img.childImageSharp.fluid.src}
                    fluid={post.frontmatter.img.childImageSharp.fluid}
                  />
                </figure>
              </div>
            )}
            <div className="wrap-content">
              <header className="header-page">
                <h1 className="page-title">{post.frontmatter.title}</h1>
                <div className="page-date">
                  <span>
                    {
                      ConvertDate(post.frontmatter.date)
                    }
                    </span>
                </div>
                <Social title={sharetitle} />
              </header>
              <div dangerouslySetInnerHTML={{ __html: post.html }} />
              <div className="page-footer">
                <div className="page-tag">
                  {post.frontmatter.tags &&
                    post.frontmatter.tags.map(tag => (
                      <span key={tag}>
                        <Link className="tag" to={`/tags/${kebabCase(tag)}/`}>#{tag}</Link>
                      </span>
                    ))}
                </div>
              </div>
              {/* <Bio siteMetadata={siteMetadata} showContent={false} /> */}
              <Social title={sharetitle} />
            </div>
          </div>
        </article>
      </DefaultLayout>
    )
  }
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
        siteUrl
        description
        social {
          twitter
          facebook
          linkedin
          github
          email
          rss
        }
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "YYYY, MMM DD")
        description
        tags
        img {
          childImageSharp {
            fluid(maxWidth: 1940, quality: 90) {
              aspectRatio
              base64
              sizes
              src
              srcSet
            }
          }
        }
      }
    }
  }
`
